"use client"

import { useState } from "react"
import { Card } from "./card"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "./tabs"
import { Switch } from "./switch"
import { Slider } from "./slider"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "./select"
import { Label } from "./label"
import { Separator } from "./separator"

interface SettingsState {
  appearance: {
    theme: "light" | "dark" | "system"
    transparency: number
    animations: boolean
  }
  system: {
    startupApps: boolean
    autoUpdates: boolean
    powerMode: "balanced" | "performance" | "powersaver"
  }
  accessibility: {
    highContrast: boolean
    reducedMotion: boolean
    fontSize: number
  }
}

export function Settings() {
  const [settings, setSettings] = useState<SettingsState>({
    appearance: {
      theme: "system",
      transparency: 80,
      animations: true,
    },
    system: {
      startupApps: true,
      autoUpdates: true,
      powerMode: "balanced",
    },
    accessibility: {
      highContrast: false,
      reducedMotion: false,
      fontSize: 100,
    },
  })

  const updateSettings = (category: keyof SettingsState, key: string, value: any) => {
    setSettings((prev) => ({
      ...prev,
      [category]: {
        ...prev[category],
        [key]: value,
      },
    }))
  }

  return (
    <Tabs defaultValue="appearance" className="w-full">
      <TabsList className="mb-4 w-full bg-gray-900/50 p-1 backdrop-blur-xl border border-blue-500/30">
        <TabsTrigger 
          value="appearance"
          className="data-[state=active]:bg-blue-600 data-[state=active]:text-white"
        >
          Appearance
        </TabsTrigger>
        <TabsTrigger 
          value="system"
          className="data-[state=active]:bg-blue-600 data-[state=active]:text-white"
        >
          System
        </TabsTrigger>
        <TabsTrigger 
          value="accessibility"
          className="data-[state=active]:bg-blue-600 data-[state=active]:text-white"
        >
          Accessibility
        </TabsTrigger>
      </TabsList>

      <TabsContent value="appearance">
        <Card className="relative overflow-hidden p-6 backdrop-blur-xl bg-gray-900/50 border border-blue-500/30">
          <div className="absolute inset-0 bg-gradient-to-br from-blue-500/10 to-purple-500/10"></div>
          <div className="relative space-y-6">
            <div className="space-y-2">
              <Label className="text-white">Theme</Label>
              <Select
                value={settings.appearance.theme}
                onValueChange={(value: "light" | "dark" | "system") =>
                  updateSettings("appearance", "theme", value)
                }
              >
                <SelectTrigger className="bg-gray-800/50 border-blue-500/30">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent className="bg-gray-900/95 border-blue-500/30 backdrop-blur-xl">
                  <SelectItem value="light">Light</SelectItem>
                  <SelectItem value="dark">Dark</SelectItem>
                  <SelectItem value="system">System</SelectItem>
                </SelectContent>
              </Select>
            </div>

            <div className="space-y-2">
              <Label className="text-white">Transparency</Label>
              <Slider
                value={[settings.appearance.transparency]}
                min={0}
                max={100}
                step={1}
                className="py-2"
                onValueChange={([value]) =>
                  updateSettings("appearance", "transparency", value)
                }
              />
            </div>

            <div className="flex items-center justify-between">
              <Label className="text-white">Enable Animations</Label>
              <Switch
                checked={settings.appearance.animations}
                onCheckedChange={(checked) =>
                  updateSettings("appearance", "animations", checked)
                }
                className="data-[state=checked]:bg-blue-600"
              />
            </div>
          </div>
        </Card>
      </TabsContent>

      <TabsContent value="system">
        <Card className="relative overflow-hidden p-6 backdrop-blur-xl bg-gray-900/50 border border-blue-500/30">
          <div className="absolute inset-0 bg-gradient-to-br from-purple-500/10 to-blue-500/10"></div>
          <div className="relative space-y-6">
            <div className="flex items-center justify-between">
              <Label className="text-white">Launch Apps on Startup</Label>
              <Switch
                checked={settings.system.startupApps}
                onCheckedChange={(checked) =>
                  updateSettings("system", "startupApps", checked)
                }
                className="data-[state=checked]:bg-blue-600"
              />
            </div>

            <div className="flex items-center justify-between">
              <Label className="text-white">Automatic Updates</Label>
              <Switch
                checked={settings.system.autoUpdates}
                onCheckedChange={(checked) =>
                  updateSettings("system", "autoUpdates", checked)
                }
                className="data-[state=checked]:bg-blue-600"
              />
            </div>

            <div className="space-y-2">
              <Label className="text-white">Power Mode</Label>
              <Select
                value={settings.system.powerMode}
                onValueChange={(value: "balanced" | "performance" | "powersaver") =>
                  updateSettings("system", "powerMode", value)
                }
              >
                <SelectTrigger className="bg-gray-800/50 border-blue-500/30">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent className="bg-gray-900/95 border-blue-500/30 backdrop-blur-xl">
                  <SelectItem value="balanced">Balanced</SelectItem>
                  <SelectItem value="performance">Performance</SelectItem>
                  <SelectItem value="powersaver">Power Saver</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>
        </Card>
      </TabsContent>

      <TabsContent value="accessibility">
        <Card className="relative overflow-hidden p-6 backdrop-blur-xl bg-gray-900/50 border border-blue-500/30">
          <div className="absolute inset-0 bg-gradient-to-br from-cyan-500/10 to-blue-500/10"></div>
          <div className="relative space-y-6">
            <div className="flex items-center justify-between">
              <Label className="text-white">High Contrast</Label>
              <Switch
                checked={settings.accessibility.highContrast}
                onCheckedChange={(checked) =>
                  updateSettings("accessibility", "highContrast", checked)
                }
                className="data-[state=checked]:bg-blue-600"
              />
            </div>

            <div className="flex items-center justify-between">
              <Label className="text-white">Reduced Motion</Label>
              <Switch
                checked={settings.accessibility.reducedMotion}
                onCheckedChange={(checked) =>
                  updateSettings("accessibility", "reducedMotion", checked)
                }
                className="data-[state=checked]:bg-blue-600"
              />
            </div>

            <div className="space-y-2">
              <Label className="text-white">Font Size ({settings.accessibility.fontSize}%)</Label>
              <Slider
                value={[settings.accessibility.fontSize]}
                min={50}
                max={200}
                step={10}
                className="py-2"
                onValueChange={([value]) =>
                  updateSettings("accessibility", "fontSize", value)
                }
              />
            </div>
          </div>
        </Card>
      </TabsContent>
    </Tabs>
  )
}
