"use client"

import { useState, useCallback } from "react"
import { Input } from "./input"
import { ScrollArea } from "./scroll-area"
import { Separator } from "./separator"
import { Button } from "./button"
import {
  ContextMenu,
  ContextMenuContent,
  ContextMenuItem,
  ContextMenuSeparator,
  ContextMenuTrigger,
} from "./context-menu"
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "./dialog"

interface FileItem {
  id: string
  name: string
  type: "file" | "folder"
  size?: string
  modified: string
  path: string
  children?: FileItem[]
}

const initialFiles: FileItem[] = [
  {
    id: "1",
    name: "Documents",
    type: "folder",
    modified: "2024-01-20",
    path: "/Documents",
    children: [
      {
        id: "1-1",
        name: "Project.pdf",
        type: "file",
        size: "2.5 MB",
        modified: "2024-01-19",
        path: "/Documents/Project.pdf",
      },
      {
        id: "1-2",
        name: "Report.docx",
        type: "file",
        size: "1.2 MB",
        modified: "2024-01-18",
        path: "/Documents/Report.docx",
      },
    ],
  },
  {
    id: "2",
    name: "Downloads",
    type: "folder",
    modified: "2024-01-17",
    path: "/Downloads",
    children: [
      {
        id: "2-1",
        name: "Image.jpg",
        type: "file",
        size: "3.7 MB",
        modified: "2024-01-16",
        path: "/Downloads/Image.jpg",
      },
    ],
  },
  {
    id: "3",
    name: "config.json",
    type: "file",
    size: "4 KB",
    modified: "2024-01-15",
    path: "/config.json",
  },
]

export function FileExplorer() {
  const [files, setFiles] = useState<FileItem[]>(initialFiles)
  const [currentPath, setCurrentPath] = useState<string[]>([])
  const [searchQuery, setSearchQuery] = useState("")
  const [selectedFile, setSelectedFile] = useState<string | null>(null)
  const [clipboardItem, setClipboardItem] = useState<{ item: FileItem; action: "copy" | "cut" } | null>(null)
  const [newItemDialog, setNewItemDialog] = useState<{ isOpen: boolean; type: "file" | "folder" }>({
    isOpen: false,
    type: "file",
  })
  const [newItemName, setNewItemName] = useState("")

  const getCurrentFiles = useCallback(() => {
    let current = files
    for (const pathPart of currentPath) {
      const folder = current.find((f) => f.name === pathPart && f.type === "folder")
      if (folder?.children) {
        current = folder.children
      }
    }
    return current.filter((file) =>
      file.name.toLowerCase().includes(searchQuery.toLowerCase())
    )
  }, [files, currentPath, searchQuery])

  const handleNavigate = (path: string[]) => {
    setCurrentPath(path)
    setSelectedFile(null)
  }

  const handleCreateItem = () => {
    if (!newItemName) return

    const newItem: FileItem = {
      id: Date.now().toString(),
      name: newItemName,
      type: newItemDialog.type,
      modified: new Date().toISOString().split("T")[0],
      path: [...currentPath, newItemName].join("/"),
      ...(newItemDialog.type === "folder" ? { children: [] } : { size: "0 KB" }),
    }

    setFiles((prev) => {
      let current = [...prev]
      let target = current
      
      for (const pathPart of currentPath) {
        const folderIndex = target.findIndex((f) => f.name === pathPart)
        if (folderIndex !== -1 && target[folderIndex].children) {
          target = target[folderIndex].children!
        }
      }
      
      target.push(newItem)
      return current
    })

    setNewItemDialog({ isOpen: false, type: "file" })
    setNewItemName("")
  }

  const handleDelete = (item: FileItem) => {
    setFiles((prev) => {
      let current = [...prev]
      let target = current
      
      for (const pathPart of currentPath) {
        const folderIndex = target.findIndex((f) => f.name === pathPart)
        if (folderIndex !== -1 && target[folderIndex].children) {
          target = target[folderIndex].children!
        }
      }
      
      const index = target.findIndex((f) => f.id === item.id)
      if (index !== -1) {
        target.splice(index, 1)
      }
      
      return current
    })
    
    setSelectedFile(null)
  }

  const handlePaste = () => {
    if (!clipboardItem) return

    const newItem: FileItem = {
      ...clipboardItem.item,
      id: Date.now().toString(),
      path: [...currentPath, clipboardItem.item.name].join("/"),
    }

    setFiles((prev) => {
      let current = [...prev]
      let target = current
      
      for (const pathPart of currentPath) {
        const folderIndex = target.findIndex((f) => f.name === pathPart)
        if (folderIndex !== -1 && target[folderIndex].children) {
          target = target[folderIndex].children!
        }
      }
      
      target.push(newItem)
      
      if (clipboardItem.action === "cut") {
        // Remove the original item
        let originalTarget = current
        const originalPath = clipboardItem.item.path.split("/").filter(Boolean)
        const itemName = originalPath.pop()
        
        for (const pathPart of originalPath) {
          const folderIndex = originalTarget.findIndex((f) => f.name === pathPart)
          if (folderIndex !== -1 && originalTarget[folderIndex].children) {
            originalTarget = originalTarget[folderIndex].children!
          }
        }
        
        const index = originalTarget.findIndex((f) => f.name === itemName)
        if (index !== -1) {
          originalTarget.splice(index, 1)
        }
      }
      
      return current
    })

    setClipboardItem(null)
  }

  const currentFiles = getCurrentFiles()

  return (
    <div className="flex h-full flex-col">
      {/* Toolbar */}
      <div className="flex items-center space-x-2 p-2 bg-gray-900/50 backdrop-blur-xl border-b border-blue-500/30">
        <Button
          variant="ghost"
          size="sm"
          onClick={() => handleNavigate(currentPath.slice(0, -1))}
          disabled={currentPath.length === 0}
          className="text-white hover:bg-blue-500/20"
        >
          Back
        </Button>
        <Separator orientation="vertical" className="h-6 bg-blue-500/30" />
        <div className="flex items-center space-x-1 text-sm text-blue-100">
          <Button
            variant="ghost"
            size="sm"
            onClick={() => handleNavigate([])}
            className="hover:bg-blue-500/20"
          >
            Root
          </Button>
          {currentPath.map((path, index) => (
            <div key={path} className="flex items-center">
              <span className="mx-1 text-blue-500">/</span>
              <Button
                variant="ghost"
                size="sm"
                onClick={() => handleNavigate(currentPath.slice(0, index + 1))}
                className="hover:bg-blue-500/20"
              >
                {path}
              </Button>
            </div>
          ))}
        </div>
      </div>

      {/* Search and Actions */}
      <div className="flex items-center justify-between p-4 bg-gray-900/30">
        <Input
          type="text"
          placeholder="Search files..."
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          className="w-64 bg-gray-800/50 border-blue-500/30"
        />
        <div className="flex space-x-2">
          <Dialog open={newItemDialog.isOpen} onOpenChange={(open) => setNewItemDialog(prev => ({ ...prev, isOpen: open }))}>
            <DialogTrigger asChild>
              <Button
                variant="outline"
                onClick={() => setNewItemDialog({ isOpen: true, type: "file" })}
                className="border-blue-500/30 hover:bg-blue-500/20"
              >
                New File
              </Button>
            </DialogTrigger>
            <DialogContent className="bg-gray-900/95 border-blue-500/30 backdrop-blur-xl">
              <DialogHeader>
                <DialogTitle>Create New {newItemDialog.type === "file" ? "File" : "Folder"}</DialogTitle>
              </DialogHeader>
              <div className="space-y-4 pt-4">
                <Input
                  type="text"
                  placeholder={`Enter ${newItemDialog.type} name...`}
                  value={newItemName}
                  onChange={(e) => setNewItemName(e.target.value)}
                  className="bg-gray-800/50 border-blue-500/30"
                />
                <div className="flex justify-end space-x-2">
                  <Button
                    variant="outline"
                    onClick={() => {
                      setNewItemDialog({ isOpen: false, type: "file" })
                      setNewItemName("")
                    }}
                    className="border-blue-500/30 hover:bg-blue-500/20"
                  >
                    Cancel
                  </Button>
                  <Button
                    onClick={handleCreateItem}
                    className="bg-blue-600 hover:bg-blue-700"
                  >
                    Create
                  </Button>
                </div>
              </div>
            </DialogContent>
          </Dialog>
          <Button
            variant="outline"
            onClick={() => setNewItemDialog({ isOpen: true, type: "folder" })}
            className="border-blue-500/30 hover:bg-blue-500/20"
          >
            New Folder
          </Button>
        </div>
      </div>

      {/* File List */}
      <ScrollArea className="flex-1">
        <div className="space-y-1 p-4">
          {currentFiles.map((file) => (
            <ContextMenu key={file.id}>
              <ContextMenuTrigger>
                <div
                  className={`
                    flex items-center justify-between rounded-lg px-4 py-2 transition-all duration-200
                    ${selectedFile === file.id ? "bg-blue-500/20" : "hover:bg-gray-800/50"}
                    cursor-pointer
                  `}
                  onClick={() => setSelectedFile(file.id)}
                  onDoubleClick={() => {
                    if (file.type === "folder") {
                      handleNavigate([...currentPath, file.name])
                    }
                  }}
                >
                  <div className="flex items-center space-x-3">
                    <span className="text-xl">
                      {file.type === "folder" ? "" : ""}
                    </span>
                    <span className="text-sm text-white">{file.name}</span>
                  </div>
                  <div className="flex items-center space-x-4 text-xs text-gray-400">
                    {file.size && <span>{file.size}</span>}
                    <span>{file.modified}</span>
                  </div>
                </div>
              </ContextMenuTrigger>
              <ContextMenuContent className="bg-gray-900/95 border-blue-500/30 backdrop-blur-xl">
                {file.type === "folder" && (
                  <ContextMenuItem
                    onClick={() => handleNavigate([...currentPath, file.name])}
                    className="hover:bg-blue-500/20"
                  >
                    Open
                  </ContextMenuItem>
                )}
                <ContextMenuItem
                  onClick={() => {
                    setClipboardItem({ item: file, action: "copy" })
                  }}
                  className="hover:bg-blue-500/20"
                >
                  Copy
                </ContextMenuItem>
                <ContextMenuItem
                  onClick={() => {
                    setClipboardItem({ item: file, action: "cut" })
                  }}
                  className="hover:bg-blue-500/20"
                >
                  Cut
                </ContextMenuItem>
                <ContextMenuSeparator className="bg-blue-500/30" />
                <ContextMenuItem
                  onClick={() => handleDelete(file)}
                  className="text-red-400 hover:bg-red-500/20"
                >
                  Delete
                </ContextMenuItem>
              </ContextMenuContent>
            </ContextMenu>
          ))}
        </div>
      </ScrollArea>

      {/* Status Bar */}
      <div className="flex items-center justify-between px-4 py-2 bg-gray-900/50 border-t border-blue-500/30 text-xs text-gray-400">
        <span>{currentFiles.length} items</span>
        {clipboardItem && (
          <div className="flex items-center space-x-2">
            <span>{clipboardItem.action === "copy" ? "Copy:" : "Cut:"} {clipboardItem.item.name}</span>
            <Button
              variant="ghost"
              size="sm"
              onClick={handlePaste}
              className="h-6 px-2 text-xs hover:bg-blue-500/20"
            >
              Paste
            </Button>
          </div>
        )}
      </div>
    </div>
  )
}
