"use client"

import { useState, useEffect } from "react"
import { Input } from "./input"
import { Button } from "./button"
import { Separator } from "./separator"

export function Taskbar() {
  const [time, setTime] = useState<Date | null>(null)
  const [searchQuery, setSearchQuery] = useState("")

  useEffect(() => {
    setTime(new Date())
    const timer = setInterval(() => {
      setTime(new Date())
    }, 1000)

    return () => clearInterval(timer)
  }, [])

  const handleSearch = (e: React.FormEvent) => {
    e.preventDefault()
    if (!searchQuery.trim()) return

    const event = new CustomEvent("openWindow", {
      detail: {
        type: "ai-assistant",
        title: "AI Assistant",
        query: searchQuery,
      },
    })
    window.dispatchEvent(event)
    setSearchQuery("")
  }

  return (
    <div className="fixed bottom-0 left-0 right-0 z-50">
      {/* Glow effect */}
      <div className="absolute inset-0 bg-gradient-to-t from-blue-500/20 to-transparent blur-xl"></div>
      
      {/* Main taskbar */}
      <div className="relative flex h-16 items-center justify-between bg-gray-900/70 backdrop-blur-xl px-4 border-t border-blue-500/30">
        {/* Start Menu & Quick Launch */}
        <div className="flex items-center space-x-4">
          <Button
            variant="ghost"
            className="h-10 w-10 rounded-full p-0 transition-transform hover:scale-110 bg-blue-500/10 hover:bg-blue-500/20 hover:shadow-[0_0_15px_rgba(59,130,246,0.5)]"
            onClick={() => {
              const event = new CustomEvent("openWindow", {
                detail: { type: "file-explorer", title: "File Explorer" },
              })
              window.dispatchEvent(event)
            }}
          >
            <img src="/window.svg" alt="Start" className="h-6 w-6" />
          </Button>
          <Separator orientation="vertical" className="h-8 bg-blue-500/30" />
          <form onSubmit={handleSearch} className="w-64">
            <Input
              type="text"
              placeholder="Search or ask anything..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="h-8 bg-white/5 border-blue-500/30 focus:border-blue-500/50 focus:ring-blue-500/30 placeholder:text-gray-400"
            />
          </form>
        </div>

        {/* System Tray */}
        <div className="flex items-center space-x-4">
          <Button
            variant="ghost"
            size="sm"
            className="text-xs text-blue-100 bg-blue-500/10 hover:bg-blue-500/20 transition-all duration-300"
            onClick={() => {
              const event = new CustomEvent("openWindow", {
                detail: { type: "system-monitor", title: "System Monitor" },
              })
              window.dispatchEvent(event)
            }}
          >
            <div className="flex items-center space-x-2">
              <div className="h-2 w-2 rounded-full bg-green-400 animate-pulse"></div>
              <span>CPU: 45% | RAM: 6.2GB</span>
            </div>
          </Button>
          <Separator orientation="vertical" className="h-8 bg-blue-500/30" />
          <Button
            variant="ghost"
            size="sm"
            className="text-xs text-blue-100 bg-blue-500/10 hover:bg-blue-500/20 transition-all duration-300"
            onClick={() => {
              const event = new CustomEvent("openWindow", {
                detail: { type: "settings", title: "Settings" },
              })
              window.dispatchEvent(event)
            }}
          >
            {time?.toLocaleTimeString() || ""}
          </Button>
        </div>
      </div>
    </div>
  )
}
