"use client"

import { useState, useRef, useEffect } from "react"
import { ScrollArea } from "./scroll-area"
import { Input } from "./input"
import { Button } from "./button"

interface Message {
  id: string
  type: "user" | "assistant" | "system"
  content: string
  timestamp: Date
  status?: "success" | "error" | "loading"
}

interface Command {
  name: string
  description: string
  execute: (args: string[]) => Promise<string>
}

const parseCommand = (input: string): { command: string; args: string[] } => {
  const parts = input.trim().split(/\s+/).filter(Boolean)
  if (parts.length === 0) return { command: "", args: [] }

  const commandPart = parts[0]
  const command = commandPart.startsWith("/") ? commandPart.substring(1) : commandPart
  const args = parts.slice(1).map(String)

  return { command, args }
}

type AppType = "file-explorer" | "settings" | "system-monitor" | string

const getFirstArg = (args: string[]): string => {
  if (!args.length) return ""
  return args[0]
}

const systemCommands: Command[] = [
  {
    name: "help",
    description: "Show available commands",
    execute: async () => {
      return "Available commands:\n" +
        systemCommands.map(cmd => `${cmd.name}: ${cmd.description}`).join("\n")
    }
  },
  {
    name: "open",
    description: "Open an application (e.g., open files, settings)",
    execute: async (args: string[]) => {
      if (!args.length) {
        return "Please specify an application to open"
      }

      const appMapping: Record<string, AppType> = {
        'files': 'file-explorer',
        'settings': 'settings',
        'monitor': 'system-monitor',
      }

      const appName = getFirstArg(args)
      if (!appName) {
        return "Please specify a valid application name"
      }

      const appType = appName.toLowerCase()
      const type = appMapping[appType] || appType
      const title = appName.charAt(0).toUpperCase() + appName.slice(1)

      const event = new CustomEvent("openWindow", {
        detail: { type, title }
      })
      window.dispatchEvent(event)
      return `Opening ${appName}...`
    }
  },
  {
    name: "system",
    description: "Show system information",
    execute: async () => {
      return "System Information:\n" +
        "OS: AI OS v1.0.0\n" +
        "CPU: Virtual Core i9\n" +
        "Memory: 16GB Virtual RAM\n" +
        "Storage: 512GB Virtual SSD"
    }
  },
  {
    name: "time",
    description: "Show current time",
    execute: async () => {
      return new Date().toLocaleString()
    }
  },
  {
    name: "clear",
    description: "Clear chat history",
    execute: async () => {
      return "CLEAR_CHAT"
    }
  }
]

export function AIAssistant() {
  const [messages, setMessages] = useState<Message[]>([
    {
      id: "1",
      type: "assistant",
      content: "Hello! I'm your AI assistant. Type 'help' to see available commands.",
      timestamp: new Date(),
    },
  ])
  const [input, setInput] = useState("")
  const [isProcessing, setIsProcessing] = useState(false)
  const [suggestions, setSuggestions] = useState<string[]>([])
  const scrollRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    if (scrollRef.current) {
      scrollRef.current.scrollTop = scrollRef.current.scrollHeight
    }
  }, [messages])

  const processCommand = async (command: string, args: string[]) => {
    const cmd = systemCommands.find(c => c.name === command.toLowerCase())
    if (cmd) {
      const result = await cmd.execute(args)
      if (result === "CLEAR_CHAT") {
        setMessages([{
          id: Date.now().toString(),
          type: "system",
          content: "Chat history cleared",
          timestamp: new Date(),
        }])
        return
      }
      return result
    }
    return `Unknown command: ${command}. Type 'help' to see available commands.`
  }

  const processMessage = async (content: string) => {
    if (content.startsWith("/")) {
      const { command, args } = parseCommand(content)
      return processCommand(command, args)
    }

    await new Promise(resolve => setTimeout(resolve, 1000))
    return `I understand you're saying "${content}". I can help you with various tasks. Try typing 'help' to see what I can do.`
  }

  const handleSendMessage = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!input.trim() || isProcessing) return

    const userMessage: Message = {
      id: Date.now().toString(),
      type: "user",
      content: input.trim(),
      timestamp: new Date(),
    }

    setMessages(prev => [...prev, userMessage])
    setInput("")
    setIsProcessing(true)
    setSuggestions([])

    try {
      const response = await processMessage(input.trim())
      
      const assistantMessage: Message = {
        id: (Date.now() + 1).toString(),
        type: "assistant",
        content: response,
        timestamp: new Date(),
        status: "success",
      }

      setMessages(prev => [...prev, assistantMessage])
    } catch (error) {
      const errorMessage: Message = {
        id: (Date.now() + 1).toString(),
        type: "assistant",
        content: "Sorry, I encountered an error processing your request.",
        timestamp: new Date(),
        status: "error",
      }
      setMessages(prev => [...prev, errorMessage])
    } finally {
      setIsProcessing(false)
    }
  }

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value
    setInput(value)

    if (value.startsWith("/")) {
      const searchTerm = value.slice(1).toLowerCase()
      const matches = systemCommands
        .filter(cmd => cmd.name.includes(searchTerm))
        .map(cmd => cmd.name)
      setSuggestions(matches)
    } else {
      setSuggestions([])
    }
  }

  return (
    <div className="flex h-full flex-col">
      <div className="p-4 bg-gray-900/50 backdrop-blur-xl border-b border-blue-500/30">
        <h3 className="text-sm font-medium text-white flex items-center space-x-2">
          <div className="h-2 w-2 rounded-full bg-green-500 animate-pulse"></div>
          <span>AI Assistant - Online</span>
        </h3>
      </div>

      <ScrollArea className="flex-1 px-4" ref={scrollRef}>
        <div className="space-y-4 py-4">
          {messages.map((message) => (
            <div
              key={message.id}
              className={`flex ${
                message.type === "user" ? "justify-end" : "justify-start"
              }`}
            >
              <div
                className={`
                  relative max-w-[80%] rounded-2xl px-4 py-3
                  ${
                    message.type === "user"
                      ? "bg-blue-600 text-white"
                      : message.type === "system"
                      ? "bg-purple-600/50 text-white"
                      : "bg-gray-800/50 text-white backdrop-blur-md"
                  }
                  ${
                    message.type === "assistant"
                      ? "before:absolute before:-left-2 before:top-4 before:h-4 before:w-4 before:rotate-45 before:bg-gray-800/50"
                      : message.type === "user"
                      ? "after:absolute after:-right-2 after:top-4 after:h-4 after:w-4 after:rotate-45 after:bg-blue-600"
                      : ""
                  }
                  border border-blue-500/30
                  shadow-[0_0_15px_rgba(59,130,246,0.2)]
                  transition-all duration-300 hover:shadow-[0_0_20px_rgba(59,130,246,0.3)]
                `}
              >
                <p className="text-sm whitespace-pre-line">{message.content}</p>
                <span className="mt-1 block text-xs opacity-50">
                  {message.timestamp.toLocaleTimeString()}
                </span>
                <div className="absolute -inset-[1px] -z-10 rounded-2xl bg-gradient-to-r from-blue-500/20 to-purple-500/20 blur-sm"></div>
              </div>
            </div>
          ))}
          {isProcessing && (
            <div className="flex justify-start">
              <div className="flex space-x-2 rounded-full bg-gray-800/50 px-4 py-2 backdrop-blur-md">
                <div className="h-2 w-2 rounded-full bg-blue-500 animate-bounce"></div>
                <div className="h-2 w-2 rounded-full bg-blue-500 animate-bounce [animation-delay:0.2s]"></div>
                <div className="h-2 w-2 rounded-full bg-blue-500 animate-bounce [animation-delay:0.4s]"></div>
              </div>
            </div>
          )}
        </div>
      </ScrollArea>

      {suggestions.length > 0 && (
        <div className="mx-4 mb-2 rounded-lg bg-gray-800/50 p-2 backdrop-blur-md border border-blue-500/30">
          {suggestions.map((suggestion) => (
            <Button
              key={suggestion}
              variant="ghost"
              size="sm"
              className="w-full justify-start text-left text-sm hover:bg-blue-500/20"
              onClick={() => setInput(`/${suggestion} `)}
            >
              /{suggestion}
            </Button>
          ))}
        </div>
      )}

      <div className="border-t border-blue-500/30 bg-gray-900/50 p-4 backdrop-blur-md">
        <form onSubmit={handleSendMessage} className="flex space-x-2">
          <Input
            type="text"
            placeholder="Type a message or command (e.g., /help)..."
            value={input}
            onChange={handleInputChange}
            disabled={isProcessing}
            className="flex-1 bg-gray-800/50 border-blue-500/30 focus:border-blue-500/50 focus:ring-blue-500/30"
          />
          <Button
            type="submit"
            disabled={isProcessing}
            className="bg-blue-600 hover:bg-blue-700 transition-all duration-300 hover:shadow-[0_0_15px_rgba(59,130,246,0.5)]"
          >
            Send
          </Button>
        </form>
      </div>
    </div>
  )
}
