"use client"

import { useState, useEffect } from "react"
import { Card } from "./card"
import { FileExplorer } from "./FileExplorer"
import { AIAssistant } from "./AIAssistant"
import { SystemMonitor } from "./SystemMonitor"
import { Settings } from "./Settings"

interface Window {
  id: string
  type: string
  title: string
  position: { x: number; y: number }
  size: { width: number; height: number }
  isMinimized: boolean
  isMaximized: boolean
  zIndex: number
}

export function WindowManager() {
  const [windows, setWindows] = useState<Window[]>([])
  const [activeWindow, setActiveWindow] = useState<string | null>(null)
  const [maxZIndex, setMaxZIndex] = useState(0)
  const [isDragging, setIsDragging] = useState(false)
  const [dragOffset, setDragOffset] = useState({ x: 0, y: 0 })

  useEffect(() => {
    const handleOpenWindow = (event: CustomEvent<{ type: string; title: string }>) => {
      const { type, title } = event.detail
      const newWindow: Window = {
        id: `${type}-${Date.now()}`,
        type,
        title,
        position: { x: 100, y: 100 },
        size: { width: 800, height: 600 },
        isMinimized: false,
        isMaximized: false,
        zIndex: maxZIndex + 1,
      }
      setWindows((prev) => [...prev, newWindow])
      setActiveWindow(newWindow.id)
      setMaxZIndex((prev) => prev + 1)
    }

    window.addEventListener("openWindow", handleOpenWindow as EventListener)
    return () => window.removeEventListener("openWindow", handleOpenWindow as EventListener)
  }, [maxZIndex])

  const handleWindowClick = (windowId: string) => {
    setActiveWindow(windowId)
    setWindows((prev) =>
      prev.map((w) => ({
        ...w,
        zIndex: w.id === windowId ? maxZIndex + 1 : w.zIndex,
      }))
    )
    setMaxZIndex((prev) => prev + 1)
  }

  const handleWindowClose = (windowId: string) => {
    setWindows((prev) => prev.filter((w) => w.id !== windowId))
    if (activeWindow === windowId) {
      setActiveWindow(null)
    }
  }

  const handleWindowMinimize = (windowId: string) => {
    setWindows((prev) =>
      prev.map((w) =>
        w.id === windowId ? { ...w, isMinimized: true } : w
      )
    )
  }

  const handleWindowMaximize = (windowId: string) => {
    setWindows((prev) =>
      prev.map((w) =>
        w.id === windowId ? { ...w, isMaximized: !w.isMaximized } : w
      )
    )
  }

  const handleMouseDown = (e: React.MouseEvent, window: Window) => {
    if (e.target instanceof HTMLElement && e.target.closest('.window-controls')) {
      return
    }
    setIsDragging(true)
    setDragOffset({
      x: e.clientX - window.position.x,
      y: e.clientY - window.position.y,
    })
    handleWindowClick(window.id)
  }

  const handleMouseMove = (e: React.MouseEvent) => {
    if (!isDragging || !activeWindow) return

    const window = windows.find((w) => w.id === activeWindow)
    if (!window) return

    setWindows((prev) =>
      prev.map((w) =>
        w.id === activeWindow
          ? {
              ...w,
              position: {
                x: e.clientX - dragOffset.x,
                y: e.clientY - dragOffset.y,
              },
            }
          : w
      )
    )
  }

  const handleMouseUp = () => {
    setIsDragging(false)
  }

  const renderWindowContent = (window: Window) => {
    switch (window.type) {
      case "file-explorer":
        return <FileExplorer />
      case "ai-assistant":
        return <AIAssistant />
      case "system-monitor":
        return <SystemMonitor />
      case "settings":
        return <Settings />
      default:
        return null
    }
  }

  useEffect(() => {
    if (isDragging) {
      document.addEventListener('mousemove', handleMouseMove as any)
      document.addEventListener('mouseup', handleMouseUp)
    } else {
      document.removeEventListener('mousemove', handleMouseMove as any)
      document.removeEventListener('mouseup', handleMouseUp)
    }
    return () => {
      document.removeEventListener('mousemove', handleMouseMove as any)
      document.removeEventListener('mouseup', handleMouseUp)
    }
  }, [isDragging])

  return (
    <div className="absolute inset-0 pointer-events-none">
      {windows.map((window) => (
        <div
          key={window.id}
          className={`
            pointer-events-auto absolute transition-all duration-300
            ${window.isMinimized ? "hidden" : ""}
            ${window.isMaximized ? "!inset-0 !m-0 !w-full !h-full" : ""}
            ${activeWindow === window.id ? "scale-100" : "scale-[0.99]"}
          `}
          style={{
            transform: `translate(${window.position.x}px, ${window.position.y}px)`,
            width: window.size.width,
            height: window.size.height,
            zIndex: window.zIndex,
          }}
        >
          <Card className="h-full w-full overflow-hidden bg-gray-900/80 backdrop-blur-xl border border-blue-500/30 shadow-[0_0_15px_rgba(59,130,246,0.3)]">
            <div
              className="flex h-10 items-center justify-between bg-gray-800/50 backdrop-blur-md px-4 cursor-move border-b border-blue-500/30"
              onMouseDown={(e) => handleMouseDown(e, window)}
            >
              <span className="text-sm font-medium text-white">{window.title}</span>
              <div className="flex space-x-2 window-controls">
                <button
                  className="rounded-full h-3 w-3 bg-yellow-400 hover:bg-yellow-500 transition-colors duration-200 hover:shadow-[0_0_8px_rgba(234,179,8,0.5)]"
                  onClick={() => handleWindowMinimize(window.id)}
                />
                <button
                  className="rounded-full h-3 w-3 bg-green-400 hover:bg-green-500 transition-colors duration-200 hover:shadow-[0_0_8px_rgba(34,197,94,0.5)]"
                  onClick={() => handleWindowMaximize(window.id)}
                />
                <button
                  className="rounded-full h-3 w-3 bg-red-400 hover:bg-red-500 transition-colors duration-200 hover:shadow-[0_0_8px_rgba(239,68,68,0.5)]"
                  onClick={() => handleWindowClose(window.id)}
                />
              </div>
            </div>
            <div className="h-[calc(100%-2.5rem)] overflow-auto p-4">
              {renderWindowContent(window)}
            </div>
          </Card>
        </div>
      ))}
    </div>
  )
}
