"use client"

import { useState } from "react"

interface DraggedIcon {
  id: string
  name: string
  icon: string
  type: string
}

export function useDesktopEvents() {
  const [draggedIcon, setDraggedIcon] = useState<DraggedIcon | null>(null)
  const [iconPositions, setIconPositions] = useState<{ [key: string]: { x: number; y: number } }>({})

  const handleIconDragStart = (e: React.DragEvent, icon: DraggedIcon) => {
    setDraggedIcon(icon)
    // Set drag image
    const dragImage = new Image()
    dragImage.src = icon.icon
    e.dataTransfer.setDragImage(dragImage, 0, 0)
  }

  const handleIconDragEnd = () => {
    setDraggedIcon(null)
  }

  const handleIconDrop = (e: React.DragEvent) => {
    e.preventDefault()
    if (!draggedIcon) return

    // Calculate new position relative to the desktop
    const desktop = e.currentTarget.getBoundingClientRect()
    const x = e.clientX - desktop.left
    const y = e.clientY - desktop.top

    // Update icon position
    setIconPositions((prev) => ({
      ...prev,
      [draggedIcon.id]: { x, y },
    }))
  }

  return {
    handleIconDragStart,
    handleIconDragEnd,
    handleIconDrop,
    iconPositions,
  }
}
