"use client"

import { useState, useEffect } from "react"
import { Card } from "./card"
import { Progress } from "./progress"
import { Separator } from "./separator"

interface SystemMetrics {
  cpu: number
  memory: number
  disk: number
  network: {
    upload: number
    download: number
  }
  processes: Array<{
    id: string
    name: string
    cpu: number
    memory: number
  }>
}

export function SystemMonitor() {
  const [metrics, setMetrics] = useState<SystemMetrics>({
    cpu: 0,
    memory: 0,
    disk: 0,
    network: {
      upload: 0,
      download: 0,
    },
    processes: [],
  })

  useEffect(() => {
    // Simulate real-time metrics updates
    const interval = setInterval(() => {
      setMetrics({
        cpu: Math.random() * 100,
        memory: 45 + Math.random() * 20,
        disk: 65 + Math.random() * 10,
        network: {
          upload: Math.random() * 10,
          download: Math.random() * 20,
        },
        processes: [
          {
            id: "1",
            name: "System",
            cpu: Math.random() * 10,
            memory: Math.random() * 1000,
          },
          {
            id: "2",
            name: "Browser",
            cpu: Math.random() * 20,
            memory: Math.random() * 2000,
          },
          {
            id: "3",
            name: "AI Assistant",
            cpu: Math.random() * 15,
            memory: Math.random() * 1500,
          },
        ],
      })
    }, 1000)

    return () => clearInterval(interval)
  }, [])

  return (
    <div className="space-y-6">
      <div className="grid grid-cols-2 gap-4">
        {/* CPU Usage */}
        <Card className="relative overflow-hidden p-4 backdrop-blur-xl bg-gray-900/50 border border-blue-500/30">
          <div className="absolute inset-0 bg-gradient-to-br from-blue-500/10 to-purple-500/10"></div>
          <div className="relative">
            <h3 className="text-sm font-medium text-white flex items-center space-x-2">
              <div className="h-2 w-2 rounded-full bg-blue-500 animate-pulse"></div>
              <span>CPU Usage</span>
            </h3>
            <div className="mt-2">
              <div className="relative h-2 w-full overflow-hidden rounded-full bg-gray-700">
                <div 
                  className="absolute left-0 top-0 h-full bg-gradient-to-r from-blue-500 to-purple-500 transition-all duration-300"
                  style={{ width: `${metrics.cpu}%` }}
                ></div>
              </div>
            </div>
            <p className="mt-2 text-sm">
              <span className="text-blue-400">{metrics.cpu.toFixed(1)}%</span>
            </p>
          </div>
        </Card>

        {/* Memory Usage */}
        <Card className="relative overflow-hidden p-4 backdrop-blur-xl bg-gray-900/50 border border-blue-500/30">
          <div className="absolute inset-0 bg-gradient-to-br from-purple-500/10 to-blue-500/10"></div>
          <div className="relative">
            <h3 className="text-sm font-medium text-white flex items-center space-x-2">
              <div className="h-2 w-2 rounded-full bg-purple-500 animate-pulse"></div>
              <span>Memory Usage</span>
            </h3>
            <div className="mt-2">
              <div className="relative h-2 w-full overflow-hidden rounded-full bg-gray-700">
                <div 
                  className="absolute left-0 top-0 h-full bg-gradient-to-r from-purple-500 to-blue-500 transition-all duration-300"
                  style={{ width: `${metrics.memory}%` }}
                ></div>
              </div>
            </div>
            <p className="mt-2 text-sm">
              <span className="text-purple-400">{metrics.memory.toFixed(1)}%</span>
            </p>
          </div>
        </Card>

        {/* Disk Usage */}
        <Card className="relative overflow-hidden p-4 backdrop-blur-xl bg-gray-900/50 border border-blue-500/30">
          <div className="absolute inset-0 bg-gradient-to-br from-cyan-500/10 to-blue-500/10"></div>
          <div className="relative">
            <h3 className="text-sm font-medium text-white flex items-center space-x-2">
              <div className="h-2 w-2 rounded-full bg-cyan-500 animate-pulse"></div>
              <span>Disk Usage</span>
            </h3>
            <div className="mt-2">
              <div className="relative h-2 w-full overflow-hidden rounded-full bg-gray-700">
                <div 
                  className="absolute left-0 top-0 h-full bg-gradient-to-r from-cyan-500 to-blue-500 transition-all duration-300"
                  style={{ width: `${metrics.disk}%` }}
                ></div>
              </div>
            </div>
            <p className="mt-2 text-sm">
              <span className="text-cyan-400">{metrics.disk.toFixed(1)}%</span>
            </p>
          </div>
        </Card>

        {/* Network */}
        <Card className="relative overflow-hidden p-4 backdrop-blur-xl bg-gray-900/50 border border-blue-500/30">
          <div className="absolute inset-0 bg-gradient-to-br from-blue-500/10 to-green-500/10"></div>
          <div className="relative">
            <h3 className="text-sm font-medium text-white flex items-center space-x-2">
              <div className="h-2 w-2 rounded-full bg-green-500 animate-pulse"></div>
              <span>Network</span>
            </h3>
            <div className="mt-2 space-y-1">
              <div className="flex justify-between text-sm">
                <span className="text-gray-400">Upload</span>
                <span className="text-green-400">{metrics.network.upload.toFixed(1)} MB/s</span>
              </div>
              <div className="flex justify-between text-sm">
                <span className="text-gray-400">Download</span>
                <span className="text-blue-400">{metrics.network.download.toFixed(1)} MB/s</span>
              </div>
            </div>
          </div>
        </Card>
      </div>

      <Separator className="bg-blue-500/30" />

      {/* Running Processes */}
      <div>
        <h3 className="mb-4 text-sm font-medium text-white">Running Processes</h3>
        <div className="space-y-2">
          {metrics.processes.map((process) => (
            <Card 
              key={process.id} 
              className="relative overflow-hidden p-3 backdrop-blur-xl bg-gray-900/50 border border-blue-500/30 transition-all duration-300 hover:bg-gray-800/50"
            >
              <div className="absolute inset-0 bg-gradient-to-r from-blue-500/5 to-purple-500/5"></div>
              <div className="relative flex items-center justify-between">
                <span className="text-sm text-white">{process.name}</span>
                <div className="flex space-x-4 text-sm">
                  <span className="text-blue-400">CPU: {process.cpu.toFixed(1)}%</span>
                  <span className="text-purple-400">Memory: {process.memory.toFixed(0)} MB</span>
                </div>
              </div>
            </Card>
          ))}
        </div>
      </div>
    </div>
  )
}
