"use client"

import { useState } from "react"
import { Card } from "./card"
import { useDesktopEvents } from "@/hooks/useDesktopEvents"

interface DesktopIcon {
  id: string
  name: string
  icon: string
  type: "file-explorer" | "ai-assistant" | "system-monitor" | "settings"
}

const desktopIcons: DesktopIcon[] = [
  {
    id: "file-explorer",
    name: "File Explorer",
    icon: "/file.svg",
    type: "file-explorer",
  },
  {
    id: "ai-assistant",
    name: "AI Assistant",
    icon: "/globe.svg",
    type: "ai-assistant",
  },
  {
    id: "system-monitor",
    name: "System Monitor",
    icon: "/window.svg",
    type: "system-monitor",
  },
  {
    id: "settings",
    name: "Settings",
    icon: "/vercel.svg",
    type: "settings",
  },
]

export function Desktop() {
  const [selectedIcon, setSelectedIcon] = useState<string | null>(null)
  const { handleIconDragStart, handleIconDragEnd, handleIconDrop } = useDesktopEvents()

  return (
    <div 
      className="absolute inset-0 bg-gradient-to-br from-gray-900 via-blue-900 to-black overflow-hidden"
      onDragOver={(e) => e.preventDefault()}
      onDrop={handleIconDrop}
    >
      {/* Animated background elements */}
      <div className="absolute inset-0">
        <div className="absolute w-[500px] h-[500px] bg-blue-500/20 rounded-full blur-3xl animate-pulse -top-20 -left-20"></div>
        <div className="absolute w-[300px] h-[300px] bg-purple-500/20 rounded-full blur-3xl animate-pulse top-1/2 right-20"></div>
        <div className="absolute w-[400px] h-[400px] bg-cyan-500/20 rounded-full blur-3xl animate-pulse bottom-0 left-1/3"></div>
      </div>

      {/* Grid layout for desktop icons */}
      <div className="grid grid-cols-4 gap-6 p-6 relative z-10">
        {desktopIcons.map((icon) => (
          <Card
            key={icon.id}
            className={`
              group flex cursor-pointer flex-col items-center justify-center p-4
              transition-all duration-300 backdrop-blur-md bg-white/5
              hover:bg-white/10 hover:scale-105 border border-blue-500/30
              ${selectedIcon === icon.id ? "bg-white/20 ring-2 ring-blue-500" : ""}
              hover:shadow-[0_0_15px_rgba(59,130,246,0.5)] hover:ring-2 hover:ring-blue-500
            `}
            draggable
            onDragStart={(e) => handleIconDragStart(e, icon)}
            onDragEnd={handleIconDragEnd}
            onClick={() => {
              setSelectedIcon(icon.id)
              const event = new CustomEvent("openWindow", {
                detail: { type: icon.type, title: icon.name },
              })
              window.dispatchEvent(event)
            }}
          >
            <div className="relative">
              <img
                src={icon.icon}
                alt={icon.name}
                className="h-16 w-16 object-contain transition-transform duration-300 group-hover:scale-110 filter drop-shadow-[0_0_8px_rgba(59,130,246,0.5)]"
                draggable={false}
              />
              <div className="absolute -inset-1 bg-blue-500/20 blur-xl rounded-full opacity-0 group-hover:opacity-100 transition-opacity duration-300"></div>
            </div>
            <span className="mt-2 text-sm font-medium text-white/90 group-hover:text-white transition-colors duration-300">
              {icon.name}
            </span>
          </Card>
        ))}
      </div>
    </div>
  )
}
